@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 800px;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 40px;
}

.footer {
  padding: 50px;
  background-color: #faebd7;
}
.footer .container {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, max-content);
  gap: 40px;
}

.footer__link {
  font-size: 20px;
  line-height: 24px;
}
.footer__link:hover, .footer__link:focus {
  color: #ff1553;
}

.form__search {
  padding: 10px;
  border-bottom: 3px solid #000000;
  text-align: center;
}

.form__wrapper {
  display: flex;
  flex-direction: column;
  padding: 50px 100px 50px 50px;
}

.form__box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  position: relative;
  border: 1px solid #000000;
  margin-bottom: 40px;
}
@media (max-width: 1320px) {
  .form__box {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 899px) {
  .form__box {
    grid-template-columns: 1fr;
  }
}
.form__box label {
  padding: 10px 0 40px;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #000000;
}
.form__box label:not(:last-child) {
  border-right: 1px solid #000000;
}

.form__label-text {
  display: block;
  border-bottom: 1px solid #000000;
  margin-bottom: 10px;
  padding: 0 20px 10px;
}

.form__input {
  margin: 0 10px;
  padding: 0 10px;
  min-height: 30px;
  border-radius: 5px;
}

.form__delete {
  position: absolute;
  right: -85px;
  top: 50%;
  transform: translate(7%);
}

.form__btn-box {
  display: flex;
  justify-content: space-between;
  padding: 0 100px 0 50px;
}

.form__button {
  border-radius: 5px;
  min-width: 200px;
}

.menu {
  border-right: 3px solid #000000;
}

.menu__title {
  margin: 0 5px;
  border-bottom: 1px solid #000000;
}

.table {
  display: grid;
  grid-template-columns: 160px 1fr;
  border: 3px solid #000000;
}

